const StaticStrokeColorEditor = {
    label: _('Static Stroke'),
    option: 'strokeColor',
    editor: 'editor.color',
    showEditor: ({ options }) => !isDynamicOption(options.strokeColor),
};

export default StaticStrokeColorEditor;
