import { _ } from '@splunk/ui-utils/i18n';
import { isDynamicOption } from '../utils/configUtils';

export const getDynamicFillColorEditor = ({ defaultContext, defaultPalettesConfig }) => ({
    label: _('Dynamic Fill'),
    option: 'fillColor',
    context: 'fillColorEditorConfig',
    editor: 'editor.dynamicColor',
    showEditor: ({ options }) => isDynamicOption(options.fillColor),
    editorProps: {
        labelPosition: 'left',
        flyoutTitle: _('Dynamic Coloring: Fill Color'),
        formatters: [
            {
                label: _('Ranges'),
                value: 'rangeValue',
                defaults: {
                    fillColor: defaultContext.fillColorConfig,
                },
            },
        ],
        dataSelectors: [
            {
                label: _('Fill Color'),
                value: 'fillDataValue',
            },
        ],
        defaultPalettesConfig,
    },
});
