import { _ } from '@splunk/ui-utils/i18n';
import { isDynamicOption } from '../utils/configUtils';

export const getDynamicStrokeColorEditor = ({ defaultContext, defaultPalettesConfig }) => ({
    label: _('Dynamic Stroke'),
    option: 'strokeColor',
    context: 'strokeColorEditorConfig',
    editor: 'editor.dynamicColor',
    showEditor: ({ options }) => isDynamicOption(options.strokeColor),
    editorProps: {
        labelPosition: 'left',
        flyoutTitle: _('Dynamic Coloring: Stroke Color'),
        formatters: [
            {
                label: _('Ranges'),
                value: 'rangeValue',
                defaults: {
                    strokeColor: defaultContext.strokeColorConfig,
                },
            },
        ],
        dataSelectors: [
            {
                label: _('Stroke Color'),
                value: 'strokeDataValue',
            },
        ],
        defaultPalettesConfig,
    },
});
